'use client'

import { removeTodo } from "app/features/todos/TodoSlice"
import { RootState, useAppDispatch, useAppSelector } from "app/lib/store"
import { getTodo, postTodo } from "app/lib/api"
import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query"

export default function TodoList(){
    const queryClient = useQueryClient()
    const query = useQuery({queryKey: ['todos'], queryFn: getTodo})

    const mutation = useMutation({
        mutationFn: postTodo,
        onSuccess: () => {
            queryClient.invalidateQueries({queryKey: ['todos']})
        }
    })
    if (!query.data) return <div>Loading...</div>;
    console.log(query.data);
    return(
        <div>
            <ul>
                {query.data.todos.todos.map((todo: any) => (
                    <li key={todo.id}>
                    {todo.todo}
                    {todo.completed ? " (done)" : ""}
                    </li>
                ))}
            </ul>
            <button onClick={() => {
                mutation.mutate({
                    id: Date.now(),
                    todo: 'Do launry',
                })
            }}>Addtodo</button>
        </div>
    )
}